export const connect = (address, retry) => {
    const ws = new WebSocket(address);
    ws.onerror = (event) => {
        console.error('---', event);
    };
    ws.onmessage = (message) => {
        console.log('---', message);
    };
    ws.onopen = (event) => {
        console.log('---', event);
        ws.send('ping');
    };
};
export default connect;
